export const id6 = () => 'v1-' + Math.random().toString(36).substring(2, 8);

export const PSEUDO_SELECTORS = [
    'hover', 'focus', 'active', 'visited', 'after', 'before',
    'first', 'last', 'nth-child', 'nth-last-child', 'nth-of-type', 'nth-last-of-type',
    'only-child', 'only-of-type', 'empty', 'target', 'checked', 'disabled', 'enabled',
    'required', 'optional', 'valid', 'invalid', 'in-range', 'out-of-range',
    'read-only', 'read-write', 'placeholder-shown', 'autofill', 'user-invalid',
    'user-valid', 'blank', 'valid', 'invalid', 'in-range', 'out-of-range',
    'read-only', 'read-write', 'placeholder-shown', 'autofill', 'user-invalid',
];

export const cssPropertyMapping = {
    m: 'margin',
    mt: 'margin-top',
    mr: 'margin-right',
    mb: 'margin-bottom',
    ml: 'margin-left',

    p: 'padding',
    pt: 'padding-top',
    pr: 'padding-right',
    pb: 'padding-bottom',
    pl: 'padding-left',

    w: 'width',
    h: 'height',

    bg: 'background',
    "bg-color": "background-color",

    z: 'z-index',

    d: 'display',
    direction: 'flex-direction'
};

export function parseDOM(configs = {
    mode: 'production',
}) {
    if (typeof document === "undefined") {
        console.warn("parseDOM() only works in the browser environment.");
        return {};
    }

    const defineClasses = Array.from(document.querySelectorAll("[class]"))
        .flatMap(el =>
            Array.from(el.classList).filter(cls =>
                [":", "[", "]"].every(t => cls.includes(t))
            )
        );

    const uniqueClasses = defineClasses.reduce((acc, cls) => {
        const pseudoSelector = [...PSEUDO_SELECTORS, ...configs?.pseudoSelectors || []].find(p => cls.includes(p + ':'));
        const classWithoutPseudoSelector = pseudoSelector ? cls.replace(`${pseudoSelector}:`, '') : cls;
        const [classString, value] = classWithoutPseudoSelector.split(/:(.+)/);
        const classList = classString.split('.');
        const property = classList.pop();
        const cssQuerySelector = cls.replace(/[^a-zA-Z0-9_-]/g, c => '\\' + c);
        const cssQuery = [...classList, cssQuerySelector].join('.');
        const queryId = id6();

        if (acc[cssQuerySelector]) return acc;

        if (configs.mode === 'production') {
            document.querySelectorAll(`.${cssQuerySelector}`).forEach(el => {
                el.classList.add(queryId);
                console.log(cls);

                el.classList.remove(cls);
            });
        }

        if (pseudoSelector) {
            acc[cssQuerySelector] = {
                query: [...classList, (configs.mode === 'production' ? queryId : cssQuerySelector), pseudoSelector].join(':'),
                property: cssPropertyMapping[property] || property,
                value: value.replace(/[\[\]]/g, "").replace(/_/g, " ")
            };
        } else {

            acc[cssQuerySelector] = {
                query: [...classList, configs.mode === 'production' ? queryId : cssQuerySelector].join('.'),
                property: cssPropertyMapping[property] || property,
                value: value.replace(/[\[\]]/g, "").replace(/_/g, " ")
            };
        }

        return acc;
    }, {});

    return uniqueClasses;
}

export function injectStyles(uniqueClasses) {
    if (typeof document === "undefined") return;

    const tag = document.createElement("style");
    tag.innerHTML = Object.entries(uniqueClasses)
        .map(([_, { query, property, value }]) =>
            `.${query}{${property}:${value};}`
        )
        .join("");

    document.body.appendChild(tag);
}


export function init() {
    const uniqueClasses = parseDOM();
    injectStyles(uniqueClasses);
}